import { defineStore } from "pinia";
import {
  addDoc,
  collection,
  doc,
  deleteDoc,
  getDoc,
  getDocs,
  increment,
  onSnapshot,
  writeBatch,
  setDoc,
  updateDoc
} from "firebase/firestore";
import { db } from "@/firebase";

const DEFAULT_LIKED = { "1": false, "2": false, "3": false };
const DEFAULT_SELECTIONS = { "1": null, "2": null, "3": null };

export const useCompleteStore = defineStore('complete', {
  state() {
    return {
      contest: {
        isConcluded: false,
        categories: {
          1: [],
          2: [],
          3: []
        }
      },
      likedCategories: { ...DEFAULT_LIKED },
      votedSelections: { ...DEFAULT_SELECTIONS },
      isLoadingContest: false,
      contestError: null,
      _unsubCostumes: null,
      _unsubContest: null
    }
  },
  actions: {
    async loadContestFromFirebase() {
      this.isLoadingContest = true;
      this.contestError = null;

      try {
        const contestDocRef = doc(db, 'contest', 'settings');
        const contestSnap = await getDoc(contestDocRef);
        if (contestSnap.exists()) {
          this.contest.isConcluded = !!contestSnap.data().isConcluded;
        }

        const costumesSnap = await getDocs(collection(db, 'costumes'));
        const categories = { 1: [], 2: [], 3: [] };

        costumesSnap.forEach((docSnap) => {
          const data = docSnap.data();
          if (!data.category) return;

          categories[data.category] = categories[data.category] || [];
          categories[data.category].push({
            id: docSnap.id,
            name: data.name,
            image: data.image,
            votes: data.votes || 0,
            category: data.category
          });
        });

        this.contest.categories = categories;
      } catch (error) {
        console.error('Error loading contest:', error);
        this.contestError = error?.message || 'Failed to load contest.';
      } finally {
        this.isLoadingContest = false;
      }
    },

    startContestListeners() {
      if (this._unsubCostumes) this._unsubCostumes();
      if (this._unsubContest) this._unsubContest();

      this._unsubCostumes = onSnapshot(collection(db, 'costumes'), (snapshot) => {
        const categories = { 1: [], 2: [], 3: [] };

        snapshot.forEach((docSnap) => {
          const data = docSnap.data();
          if (!data.category) return;

          categories[data.category] = categories[data.category] || [];
          categories[data.category].push({
            id: docSnap.id,
            name: data.name,
            image: data.image,
            votes: data.votes || 0,
            category: data.category
          });
        });

        this.contest.categories = categories;
      }, (error) => {
        console.error('Costume listener error:', error);
      });

      const contestDocRef = doc(db, 'contest', 'settings');
      this._unsubContest = onSnapshot(contestDocRef, (docSnap) => {
        if (docSnap.exists()) {
          this.contest.isConcluded = !!docSnap.data().isConcluded;
        }
      }, (error) => {
        console.error('Contest listener error:', error);
      });
    },

    stopContestListeners() {
      if (this._unsubCostumes) this._unsubCostumes();
      if (this._unsubContest) this._unsubContest();
      this._unsubCostumes = null;
      this._unsubContest = null;
    },

    async addCostume({ category, name, image }) {
      if (!category || !name || !image) return;

      await addDoc(collection(db, 'costumes'), {
        category,
        name,
        image,
        votes: 0
      });
    },

    async deleteCostume(costumeId) {
      if (!costumeId) return;
      await deleteDoc(doc(db, 'costumes', costumeId));
    },

    async voteForCostume(category, costumeId) {
      if (this.contest.isConcluded) return;
      if (!costumeId) return;

      const costumeRef = doc(db, 'costumes', costumeId);
      await updateDoc(costumeRef, { votes: increment(1) });
    },

    async concludeContest() {
      const contestDocRef = doc(db, 'contest', 'settings');
      await setDoc(contestDocRef, { isConcluded: true }, { merge: true });
    },

    async resetContest() {
      const costumesSnap = await getDocs(collection(db, 'costumes'));
      const batch = writeBatch(db);

      costumesSnap.forEach((docSnap) => {
        batch.update(docSnap.ref, { votes: 0 });
      });

      const contestDocRef = doc(db, 'contest', 'settings');
      batch.set(contestDocRef, { isConcluded: false }, { merge: true });

      await batch.commit();

      this.likedCategories = { ...DEFAULT_LIKED };
      this.votedSelections = { ...DEFAULT_SELECTIONS };
      this.saveLikedCategoriesToLocalStorage();
      this.saveVotedSelectionsToLocalStorage();
    },

    loadLikedCategoriesFromLocalStorage() {
      const saved = localStorage.getItem('likedCategories');
      this.likedCategories = saved
        ? JSON.parse(saved)
        : { ...DEFAULT_LIKED };
    },

    saveLikedCategoriesToLocalStorage() {
      localStorage.setItem(
        'likedCategories',
        JSON.stringify(this.likedCategories)
      );
    },

    loadVotedSelectionsFromLocalStorage() {
      const saved = localStorage.getItem('votedSelections');
      this.votedSelections = saved
        ? JSON.parse(saved)
        : { ...DEFAULT_SELECTIONS };
    },

    saveVotedSelectionsToLocalStorage() {
      localStorage.setItem(
        'votedSelections',
        JSON.stringify(this.votedSelections)
      );
    },

    markCategoryLiked(category, costumeId) {
      const cat = category.toString();
      this.likedCategories[cat] = true;
      if (costumeId) {
        this.votedSelections[cat] = costumeId;
        this.saveVotedSelectionsToLocalStorage();
      }
      this.saveLikedCategoriesToLocalStorage();
    },

    hasVotedInCategory(category) {
      const cat = category.toString();
      return !!this.likedCategories[cat];
    }
  },
  getters: {
    getCostumesByCategory: (state) => {
      return (category) => state.contest.categories[category] || [];
    },
    isContestConcluded(state) {
      return state.contest.isConcluded;
    },
    getRankedResults: (state) => {
      return (category) => {
        return [...(state.contest.categories[category] || [])]
          .sort((a, b) => b.votes - a.votes);
      };
    },
    getVotedSelections(state) {
      return state.votedSelections;
    }
  }
});
