</template>

<script>
  import { useCompleteStore } from '@/store/store';

  export default {
    name: 'App',
    data() {
      return {
        store: useCompleteStore()
      }  
    },
    mounted() {
      this.store.loadContestFromFirebase();
      this.store.startContestListeners();
      this.store.loadLikedCategoriesFromLocalStorage();
      this.store.loadVotedSelectionsFromLocalStorage();
    },
    beforeUnmount() {
      this.store.stopContestListeners();
    }
  }
</script>

