import { createRouter, createWebHashHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    redirect: '/category/1'
  },
  {
    path: '/category/1',
    name: 'category1',
    component: () => import(/* webpackChunkName: "category1" */ '@/views/CategoryOne.vue')
  },
  {
    path: '/category/2',
    name: 'category2',
    component: () => import(/* webpackChunkName: "category2" */ '@/views/CategoryTwo.vue')
  },
  {
    path: '/category/3',
    name: 'category3',
    component: () => import(/* webpackChunkName: "category3" */ '@/views/CategoryThree.vue')
  },
  {
    path: '/admin',
    name: 'admin',
    component: () => import(/* webpackChunkName: "admin" */ '@/views/AdminView.vue')
  },
  {
    path: '/results',
    name: 'results',
    component: () => import(/* webpackChunkName: "results" */ '@/views/ResultsView.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
